#players {
  width: 100%;
  margin: 20px 0;
  .container {
    width: 100%;
    max-width: 1450px;
    margin: 0 auto;
    justify-content: center;
    border: 5px solid #fff;
    background: white;
    border-radius: 40px;
    .gap {
      width: 100%;
      display: flex;
      flex-flow: row wrap;
      .player {
        width: 25%;
        padding: 10px;
        .player_img {
          width: 100%;
          border-bottom: 5px solid #fff;
          img {
            width: 100%;
          }
        }
        .player_name {
          width: 100%;
          padding: 20px 0;
          margin: 0 auto;
          text-align: center;
          font-size: 30px;
          font-weight: 800;
          color: #333;
        }
      }
    }
  }
}