#notice {
    width: 100%;
    height: 42px;
    background: black;
    transition: all 0.6s ease-in-out;
    opacity: 0.8;
    &.on {
        margin-top: -42px;
    }
    .container {
        width: 100%;
        margin: 0 auto;
        max-width: 1050px;
        text-align: center;
        position: relative;
        display: flex;
        h1 {
            width:100%;
            line-height: 42px;
            color: #fff;
            font-size: 14px;
            font-weight: 500;
            display: flex;
            justify-content: center;
            strong {
                font-weight: 900;
                padding-left: 10px;
            }
        }
        button {
                position: absolute;
                right: 0;
                width: 20px;
                height: 20px;
                opacity: 0.6;
            }
    }
}