#main {
  width: 100%;
  .container {
    max-width: 1500px;
    background: #fff;
    border-radius: 40px;
    .main-gap {
      margin: 30px 0;
      padding: 0 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-flow: row wrap;
      li {
        border: 1px solid #fff;
        border-radius: 40px;
        overflow: auto;
        .main-players {
          width: 500px;
          height: 500px;
          .playerList {
            .img-box {
              img {
                margin: 0 auto;
                text-align: center;
                width: 100px;
              }
            }
            .name-box {
              width: 100%;
              margin: 0 auto;
              text-align: center;
            }
          }
        }
        .main-match {
          width: 500px;
          height: 500px;
        }
        .main-manage {
          width: 500px;
          height: 500px;
        }
        .main-community {
          width: 500px;
          height: 500px;
        }
      }
    }
  }
}