#header {
  width: 100%;
  margin: 20px auto;
  &:hover {
    transition: all 0.6s ;
  }
  .container {
    max-width: 1050px;
    margin: 0 auto;
    .gap {
      width: 100%;
      padding: 0 20px;
      display: flex;
      justify-content: space-between;
      .logo {
        width: 225px;
        a {
          display: block;
          img {
            width: 5rem;
          }
        }
      }
      .menu_btn {
        width: 825px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        justify-items: center;
        left: 30%;
        ul {
          display: flex;
          justify-content:space-evenly;
          li {
            width: 150px;
            padding: 5px;
            list-style: none;
            text-align: center;
            a{
              font-size: 20px;
              font-weight: bold;
              color: white;
              cursor: pointer;
              text-decoration: underline green;
            }
          }
        }
      }
    }
  }
}